import {
  LOGIN_USER,
  REGISTER,
  AUTH_USER,
  G_REGISTER,
  LOGOUT,
  IMG_UPLOAD,
  USER_EDIT,
} from "../_actions/types";

export default function (state = {}, action) {
  switch (action.type) {
    // 
    case LOGIN_USER:
      return { ...state, loginSuccess: action.payload };
    // 
    case REGISTER:
      return { ...state, register: action.payload };
    case G_REGISTER:
      return { ...state, success: action.payload };
    case IMG_UPLOAD:
      return { ...state, success: action.payload };
    case USER_EDIT:
      return { ...state, success: action.payload };
    // 
    case AUTH_USER:
      return { ...state, userData: action.payload };
    //  
    case LOGOUT:
      return { ...state, success: action.payload };
    default:
      return state;
  }
}
