import {
  BORDER_SAVE,
  BORDER_GET,
  BORDER_POST,
  BORDER_INFO,
  BORDER_EDIT,
  BORDER_DELETE,
  MBORDER_GET,
} from "../_actions/types";

export default function (state = {}, action) {
  switch (action.type) {
    // 
    case BORDER_SAVE:
      return { ...state, success: action.payload };
    // 
    case BORDER_GET:
      return { ...state, success: action.payload };
    case BORDER_POST:
      return { ...state, success: action.payload };
    // 
    case BORDER_INFO:
      return { ...state, success: action.payload };
    // 
    case BORDER_EDIT:
      return { ...state, success: action.payload };
    // 
    case BORDER_DELETE:
      return { ...state, success: action.payload };
    // 
    case MBORDER_GET:
      return { ...state, success: action.payload };

    default:
      return state;
  }
}
