import React, { useEffect, useState } from "react";
import { withRouter, Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { borderinfo, bdelete } from "../../_actions/post_actions";
import { getComment } from "../../_actions/comment_actions";
import { Spinner, Button, ButtonGroup, Form, Modal } from "react-bootstrap";
import Comment from "./Comment";
import "../Main/css/header.css";
import "./css/Info.css";

import Moment from "react-moment";
import "moment-timezone";

function BorderInfo(props) {
  //bootstrap modal
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const user = useSelector((state) => state.user);
  //:postId  
  const postId = props.match.params.postId;

  //  
  const [Info, setInfo] = useState([]);
  const dispatch = useDispatch();
  const [CommentLists, setCommentLists] = useState([]);

  //   UserInfo :postId 
  const UserInfo = { postId: postId };
  useEffect(() => {
    const head_medium = document.querySelector(".head_medium");
    const header = document.querySelector(".header");
    head_medium.classList.add("active");
    header.classList.add("active");

    dispatch(getComment(UserInfo)).then((response) => {
      // success:true 
      if (response.payload.success) {
        //setInfo   findOne     

        setCommentLists(response.payload.comments);
        //
      } else {
        alert("  ");
      }
    });
    //redux        
    dispatch(borderinfo(UserInfo)).then((response) => {
      // success:true 
      if (response.payload.success) {
        //setInfo   findOne     

        setInfo(response.payload.post);

        //
      } else {
        alert("  ");
      }
    });
  }, []);
  const updateComment = (newComment) => {
    setCommentLists(CommentLists.concat(newComment));
  };
  // 
  const InfoDelete = (event) => {
    event.preventDefault();
    // UserInfo    
    dispatch(bdelete(UserInfo)).then((response) => {
      // success:ture 
      if (response.payload.success) {
        console.log(response.payload.post);
        props.history.push("/");
      } else {
        alert("");
      }
    });
  };

  //     bootstrap 

  if (!Info.writer && !Info.title) {
    return (
      <div className="info">
        <Spinner animation="border" variant="primary" />
      </div>
    );
    //   
  } else {
    return (
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
          marginTop: "80px",
        }}
      >
        <Form className="fo">
          <Form.Group className="gr">
            <Form.Group
              style={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
              }}
            >
              {/* :postId    */}
              <Form.Label> {Info.title}</Form.Label>
              {/*    */}
              <Link to="/">
                <Button variant="secondary"></Button>
              </Link>
            </Form.Group>
            <Form.Group
              style={{
                display: "flex",
                marginTop: "10px",
              }}
            >
              {/* :postId    */}
              <Form.Label> {Info.writer.name}</Form.Label>
              {/* :postId    */}
              <Form.Label style={{ marginLeft: "15px" }}>
                
                <Moment format="YYYY.MM.DD HH:mm">{Info.createdAt}</Moment>
              </Form.Label>
            </Form.Group>
          </Form.Group>

          <Form.Group>
            {/* :postId    */}
            <Form.Control
              as="textarea"
              aria-label="With textarea"
              readonly="readonly"
              disabled="disabled"
              style={{
                height: "290px",
                resize: "none",
                background: "none",
                border: "none",
              }}
            >
              {Info.body}
            </Form.Control>
          </Form.Group>
          <Form.Group>
            {/* useSelector         */}
            {user.userData && user.userData._id === Info.writer._id ? (
              <ButtonGroup aria-label="Basic example">
                <Link to={`/edit/${Info._id}`}>
                  <Button variant="primary"></Button>
                </Link>
                <Button variant="secondary" onClick={handleShow}>
                  
                </Button>
                {/*     modal */}
                <Modal show={show} onHide={handleClose}>
                  <Modal.Header closeButton>
                    <Modal.Title></Modal.Title>
                  </Modal.Header>
                  <Modal.Body>  ?</Modal.Body>
                  <Modal.Footer>
                    <Button variant="secondary" onClick={InfoDelete}>
                      
                    </Button>
                    <Button variant="primary" onClick={handleClose}>
                      
                    </Button>
                  </Modal.Footer>
                </Modal>
              </ButtonGroup>
            ) : (
              //   
              <div></div>
            )}
          </Form.Group>
          <Comment
            Info={Info}
            CommentLists={CommentLists}
            postId={Info._id}
            refreshFunction={updateComment}
          />
        </Form>
      </div>
    );
  }
}

export default withRouter(BorderInfo);
