import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { Modal, Button } from "react-bootstrap";
import { register } from "../../_actions/user_actions";
import { withRouter } from "react-router-dom";
import Drop from "./Drop";
import "./RegisterPage.css";

function RegisterPage() {
  const [show, setShow] = useState(false);

  const handleClose = () => {
    setShow(false);
    setName("");
    setEmail("");
    setPassword("");
    setPassCh("");
  };
  const handleShow = () => setShow(true);
  //dispatch   
  const dispatch = useDispatch();
  //    useState   
  const [Message, setMessage] = useState("");
  const [Name, setName] = useState("");
  const [Email, setEmail] = useState("");
  const [Password, setPassword] = useState("");
  const [PassCh, setPassCh] = useState("");
  const [Image, setImage] = useState("");
  //onChange  value   useState  
  const NameHandler = (event) => {
    setName(event.currentTarget.value);
  };
  const EmailHandler = (event) => {
    setEmail(event.currentTarget.value);
  };
  const PassHandler = (event) => {
    setPassword(event.currentTarget.value);
  };
  const PassChHandler = (event) => {
    setPassCh(event.currentTarget.value);
  };

  const uploadImage = (newImage) => {
    setImage(newImage);
  };
  //,    
  const check_spc = /[~!@#$%^&*()_+|<>?:{}]/;
  const check_num = /[0-9]/;

  //  
  const submitHandler = (event) => {
    event.preventDefault();

    //Input     

    //Input    body  
    let body = {
      name: Name,
      email: Email,
      password: Password,
      image: Image,
    };
    // console.log(body);
    //  redux    
    if (!Name) {
      setMessage(" ");
    } else if (check_spc.test(Name) || check_num.test(Name)) {
      setMessage(" ,  ");
    } else if (!Email) {
      setMessage(" ");
    } else if (!Password) {
      setMessage(" ");
    } else if (Password !== PassCh) {
      setMessage("    ");
    } else {
      dispatch(register(body)).then((response) => {
        // success: ture 
        if (response.payload.success) {
          // console.log(response);
          alert(" ");
          setShow(false);
          // success:false 
        } else {
          setMessage("  ");
        }
      });
    }
  };

  return (
    <>
      <Button
        variant="link"
        onClick={handleShow}
        style={{ textDecoration: "none", margin: "0px", color: "black" }}
      >
        
      </Button>

      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <form
            className="FormBack"
            style={{ display: "flex", flexDirection: "column" }}
            onSubmit={submitHandler}
          >
            <h1
              style={{ fontSize: "50px", margin: "0px", textAlign: "center" }}
            >
              
            </h1>
            <Drop imageFunction={uploadImage} />
            {/*    Input */}
            <input
              className="inputEmail"
              type="text"
              value={Name}
              onChange={NameHandler}
              placeholder=""
              maxLength="6"
            />
            {/*    Input */}
            <input
              className="inputEmail"
              type="email"
              value={Email}
              onChange={EmailHandler}
              placeholder=""
            />
            {/*    Input */}
            <input
              className="inputPass"
              type="password"
              value={Password}
              minLength="5"
              onChange={PassHandler}
              placeholder=""
            />
            {/*     Input */}
            <input
              className="inputPass"
              type="password"
              value={PassCh}
              onChange={PassChHandler}
              placeholder=" "
            />

            <br />
            <label style={{ color: "red", margin: "0px", padding: "0px" }}>
              {Message}
            </label>

            {/*     */}
            <Button className="btnLogin" type="submit">
              Register
            </Button>
          </form>
        </Modal.Body>
      </Modal>
    </>
  );
}
export default withRouter(RegisterPage);
