import React, { useState, useEffect } from "react";
import { withRouter, Link } from "react-router-dom";
import { Element } from "react-scroll";
import { Table, Button, Image } from "react-bootstrap";
import { useDispatch } from "react-redux";
import {
  borderget,
  bordersearch,
  mborderget,
} from "../../_actions/post_actions";
import { Input } from "antd";
import refresh from "./img/refresh.svg";
import "./css/BList.css";
import Moment from "react-moment";
import "moment-timezone";
import PostsList from "./PostsList";
import Pagination from "./Pagination";
import ScrollAnimation from "react-animate-on-scroll";
function BorderList() {
  //   
  const { Search } = Input;
  // 
  const [List, setList] = useState([]);
  // 
  const [mlist, setMlist] = useState([]);
  //   
  const [loading, setLoading] = useState(false);
  //  
  const [currentPage, setCurrentPage] = useState(1);
  const [postsPerPage] = useState(10);
  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;
  const currentPosts = List.slice(indexOfFirstPost, indexOfLastPost);
  const paginate = (pageNumber) => setCurrentPage(pageNumber);
  const dispatch = useDispatch();
  //Search props
  const [Searchv, setSearchv] = useState("");
  // 
  const [Skip, setSkip] = useState(0);
  const [Limit, setLimit] = useState(6);
  const [PostSize, setPostSize] = useState(0);
  const searchHandler = (event) => {
    setSearchv(event.currentTarget.value);
  };

  const [SearchView, setSearchView] = useState(false);
  //   

  // 
  const updateSearch = () => {
    const body = {
      searchTerm: Searchv,
    };
    dispatch(bordersearch(body)).then((response) => {
      // success:true 
      setLoading(true);
      if (response.payload.success) {
        //   borderlist List 
        setSearchView(true);
        setList(response.payload.borderlist);
        setMlist(response.payload.borderlist);
        setLoading(false);
        setPostSize(response.payload.postSize);
        //success:false 
      } else {
        alert("");
      }
    });
  };

  useEffect(() => {
    //   
    const head_medium = document.querySelector(".head_medium");
    const header = document.querySelector(".header");

    dispatch(borderget()).then((response) => {
      // success:true 
      setLoading(true);
      if (response.payload.success) {
        //   borderlist List 
        setList(response.payload.borderlist);
        head_medium.classList.remove("active");
        header.classList.remove("active");
        setLoading(false);

        //success:false 
      } else {
        alert("");
      }
    });
    //  
    let body = {
      skip: Skip,
      limit: Limit,
    };
    getmborder(body);
  }, []);
  //  
  const getmborder = (body) => {
    dispatch(mborderget(body)).then((response) => {
      // success:true 
      if (response.payload.success) {
        //   borderlist List 
        if (body.loadMore) {
          setMlist([...mlist, ...response.payload.mborderlist]);
          setSearchView(false);
        } else {
          setMlist(response.payload.mborderlist);
        }
        setSearchView(false);
        setPostSize(response.payload.postSize);
        //success:false 
      } else {
        alert("");
      }
    });
  };
  // 
  const reload = () => {
    dispatch(borderget()).then((response) => {
      // success:true 
      setLoading(true);
      if (response.payload.success) {
        //   borderlist List 
        setSearchView(false);
        setList(response.payload.borderlist);
        setLoading(false);

        //success:false 
      } else {
        alert("");
      }
    });
  };
  //  
  const mreload = () => {
    let body = {
      skip: 0,
      limit: 6,
    };
    getmborder(body);
  };
  // 
  const loadmore = () => {
    let skip = Skip + Limit;
    let body = {
      skip: Skip,
      limit: Limit,
      loadMore: true,
    };
    getmborder(body);
    setSkip(skip);
  };
  //     
  const Mobile = mlist.map((post, index) => {
    return (
      <tbody key={post._id} style={{ width: "100%", tableLayout: "fixed" }}>
        <tr className="Mtr">
          <td className="TitleC1">
            <Link
              to={`/${post._id}`}
              style={{
                listStyleType: "none",
                textDecoration: "none",
                color: "black",
              }}
            >
              <p style={{ margin: "0px" }}>{post.title}</p>
              <p style={{ margin: "0px", fontSize: "11px", color: "gray" }}>
                {post.writer.name}
                <Moment format="YYYY.MM.DD HH:mm">{post.createdAt}</Moment>
              </p>
            </Link>
          </td>
        </tr>
      </tbody>
    );
  });

  return (
    <div>
      <Element id="boader" />

      <ScrollAnimation animateIn="fadeIn" animateOnce={true}>
        <div className="none">
          <h1 className="h1Text"></h1>
          {/*   */}
          <div className="ListTop">
            <Link to="/Write">
              <Button variant="dark"></Button>
            </Link>
            <div>
              {SearchView && (
                <Button
                  onClick={reload}
                  className="refresh"
                  style={{ background: "none", border: "none" }}
                >
                  <Image src={refresh} style={{ width: "20px" }} />
                </Button>
              )}
              <Search
                placeholder="Search"
                value={Searchv}
                onChange={searchHandler}
                onSearch={updateSearch}
                style={{ width: 200 }}
              />
            </div>
          </div>
          {/*   */}

          <div className="Blist">
            <Table
              striped
              bordered
              hover
              size="sm"
              style={{ width: "800px", tableLayout: "fixed" }}
            >
              <thead>
                <tr>
                  <th style={{ textAlign: "center", width: "300px" }}></th>
                  <th style={{ width: "150px" }}></th>
                  <th style={{ width: "150px", textAlign: "center" }}></th>
                </tr>
              </thead>
              <PostsList List={currentPosts} loading={loading} paginate />
            </Table>
          </div>

          <div className="BEdit">
            <ul style={{ width: "800px", display: "flex" }}>
              {/*   props    */}

              <li style={{ margin: "0 auto" }}>
                <Pagination
                  postsPerPage={postsPerPage}
                  totalPosts={List.length}
                  paginate={paginate}
                />
              </li>
            </ul>
          </div>
        </div>
      </ScrollAnimation>
      {/*  ,    */}
      <div>
        <ScrollAnimation animateIn="fadeIn" animateOnce={true}>
          <h1 className="hText"></h1>
          <div className="BEditM">
            <Link to="/Write">
              <Button variant="dark"></Button>
            </Link>

            <div>
              {SearchView && (
                <Button
                  onClick={mreload}
                  className="refresh"
                  style={{ background: "none", border: "none" }}
                >
                  <Image src={refresh} style={{ width: "20px" }} />
                </Button>
              )}
              <Search
                placeholder="Search"
                value={Searchv}
                onChange={searchHandler}
                onSearch={updateSearch}
                style={{ width: 200 }}
              />
            </div>
          </div>
          <div className="BMobile">
            <Table
              striped
              bordered
              hover
              size="sm"
              style={{ width: "100%", tableLayout: "fixed" }}
            >
              <thead>
                <tr>
                  <th style={{ textAlign: "center" }}></th>
                </tr>
              </thead>
              {Mobile}

              <div style={{ display: "flex", justifyContent: "center" }}>
                {PostSize >= Limit ? (
                  <Button variant="dark" onClick={loadmore}>
                    
                  </Button>
                ) : (
                  <div>
                    <Button
                      onClick={mreload}
                      className="refresh"
                      style={{ background: "none", border: "none" }}
                    >
                      <Image src={refresh} style={{ width: "20px" }} />
                    </Button>
                  </div>
                )}
              </div>
            </Table>
          </div>
        </ScrollAnimation>
      </div>
    </div>
  );
}

export default withRouter(BorderList);
