import React, { useEffect, useState } from "react";
import { withRouter, Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { borderinfo } from "../../_actions/post_actions";
import { borderedit } from "../../_actions/post_actions";
import {
  Spinner,
  Button,
  ButtonGroup,
  Form,
  FormControl,
} from "react-bootstrap";
import Moment from "react-moment";
import "moment-timezone";
import "./css/BEdit.css";

function BorderInfo(props) {
  //:postId  
  const user = useSelector((state) => state.user);
  const postId = props.match.params.postId;
  //   UserInfo :postId 
  const UserInfo = { postId: postId };
  //  
  const [Info, setInfo] = useState([]);
  const [Title, setTitle] = useState("");
  const [Body, setBody] = useState("");
  const dispatch = useDispatch();

  const TitleHanler = (event) => {
    setTitle(event.currentTarget.value);
  };
  const BodyHanler = (event) => {
    setBody(event.currentTarget.value);
  };
  useEffect(() => {
    //   
    dispatch(borderinfo(UserInfo)).then((response) => {
      if (response.payload.success) {
        setInfo(response.payload.post);
        setTitle(response.payload.post.title);
        setBody(response.payload.post.body);
      } else {
        alert("");
      }
    });
  }, []);
  //   
  const BorderEdit = (event) => {
    event.preventDefault();
    // ,   UserEdit 
    const body = { postId: postId, title: Title, body: Body };
    //   
    dispatch(borderedit(body)).then((response) => {
      // success:true 
      if (response.payload.success) {
        //,    
        console.log(response.payload.post);
        setTitle(response.payload.post);
        setBody(response.payload.post);
        //  
        props.history.push(`/${postId}`);
      } else {
        //
        alert("");
      }
    });
  };
  //   bootstrap 

  if ((!Info.writer && !Info.title) || !user.userData.isAuth) {
    return (
      <div className="info">
        <Spinner animation="border" variant="primary" />
      </div>
    );
  } else {
    return (
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
        }}
      >
        <Form className="fo1">
          <Form.Group>
            <Form.Group className="EditTitle">
              <Form.Label
                value={Title}
                onChange={TitleHanler}
                style={{ width: "40px" }}
              >
                
              </Form.Label>
              <FormControl
                value={Title}
                onChange={TitleHanler}
                className="EditInput1"
              />
            </Form.Group>
            <Form.Group
              style={{
                display: "flex",
                justifyContent: "end",
                textAlign: "right",
              }}
            >
              <Form.Label> {Info.writer.name}</Form.Label>
              <Form.Label style={{ marginLeft: "15px" }}>
                 <Moment format="YY/MM/DD HH:mm">{Info.createdAt}</Moment>
              </Form.Label>
            </Form.Group>
          </Form.Group>
          <Form.Group>
            <FormControl
              as="textarea"
              aria-label="With textarea"
              value={Body}
              onChange={BodyHanler}
              style={{ height: "290px", resize: "none" }}
            />
          </Form.Group>
          <Form.Group>
            <ButtonGroup aria-label="Basic example">
              <Button variant="primary" onClick={BorderEdit}>
                
              </Button>
              <Link to="/">
                <Button variant="secondary"></Button>
              </Link>
            </ButtonGroup>
          </Form.Group>
        </Form>
      </div>
    );
  }
}

export default withRouter(BorderInfo);
