import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { Modal, Button } from "react-bootstrap";
import { loginUser } from "../../_actions/user_actions";
import { withRouter } from "react-router-dom";
import SosialLogin from "./SosialLogin";
import "./LoginPage.css";
function LoginPage(props) {
  const [show, setShow] = useState(false);
  const handleClose = () => {
    setShow(false);

    setEmail("");
    setPassword("");
  };
  const handleShow = () => setShow(true);
  const dispatch = useDispatch();
  //   Input
  const [Email, setEmail] = useState("");
  const [Password, setPassword] = useState("");
  const emailHandler = (event) => {
    setEmail(event.currentTarget.value);
  };
  const passHandler = (event) => {
    setPassword(event.currentTarget.value);
  };

  //   
  const submitHandler = (event) => {
    event.preventDefault();
    // useState     
    let body = {
      email: Email,
      password: Password,
    };
    // 
    dispatch(loginUser(body)).then((response) => {
      // success:true 
      if (response.payload.success) {
        window.localStorage.setItem("x_token", response.payload.token);
        window.localStorage.setItem("userId", response.payload.userId);
        window.location.reload(true);
        // success:false 
      } else {
        alert("  ,  .");
      }
    });
  };
  return (
    <>
      <Button
        variant="link"
        onClick={handleShow}
        style={{ textDecoration: "none", margin: "0px", color: "black" }}
      >
        
      </Button>

      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <form className="FormBack" onSubmit={submitHandler}>
            <label className="LoginTitle"></label>
            {/*    Input  */}
            <input
              className="inputEmail"
              type="email"
              value={Email}
              onChange={emailHandler}
              placeholder=""
            />
            {/*    Input */}
            <input
              className="inputPass"
              type="password"
              value={Password}
              onChange={passHandler}
              placeholder=""
            />

            <Button className="btnLogin" type="submit">
              
            </Button>

            <SosialLogin />
          </form>
        </Modal.Body>
      </Modal>
    </>
  );
}
export default withRouter(LoginPage);
