import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { withRouter, Link } from "react-router-dom";
import { bordersave } from "../../_actions/post_actions";
import {
  Spinner,
  Button,
  ButtonGroup,
  FormControl,
  Form,
} from "react-bootstrap";
import "./css/Info.css";
function BorderWrite(props) {
  //user     
  const user = useSelector((state) => state.user);
  const dispatch = useDispatch();
  //   value    
  const [BorderTitle, setBorderTitle] = useState("");
  const [BorderText, setBorderText] = useState("");
  //  onChange   value 
  const TitleHandler = (event) => {
    setBorderTitle(event.currentTarget.value);
  };
  const TextHandler = (event) => {
    setBorderText(event.currentTarget.value);
  };
  useEffect(() => {
    const head_medium = document.querySelector(".head_medium");
    const header = document.querySelector(".header");
    head_medium.classList.add("active");
    header.classList.add("active");
  }, []);

  //   

  const BoderSubmit = (event) => {
    event.preventDefault();
    //   
    if (!BorderTitle) {
      alert(" ");
    } else {
      //   body  
      const body = {
        //   
        writer: user.userData._id,
        title: BorderTitle,
        body: BorderText,
      };
      //redux  
      dispatch(bordersave(body)).then((response) => {
        // success: true 
        if (response.payload.success) {
          //    success:true  modal 

          //      
          setBorderTitle("");
          setBorderText("");
          //props       refesh
          // props.refesh(response.payload.result);
          props.history.push("/");
          //
        } else {
          console.log(response);
          alert("");
        }
      });
    }
  };
  if (!user.userData.isAuth) {
    return (
      <div className="info">
        <Spinner animation="border" variant="primary" />
      </div>
    );
  } else {
    return (
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
        }}
      >
        <Form className="fo1">
          <Form.Group>
            <Form.Group className="EditTitle">
              <Form.Label style={{ width: "40px" }}></Form.Label>
              <FormControl
                value={BorderTitle}
                onChange={TitleHandler}
                className="EditInput1"
              />
            </Form.Group>
            <Form.Group
              style={{
                display: "flex",
                justifyContent: "end",
                textAlign: "right",
              }}
            ></Form.Group>
          </Form.Group>
          <Form.Group>
            <FormControl
              as="textarea"
              aria-label="With textarea"
              value={BorderText}
              onChange={TextHandler}
              style={{ height: "290px", resize: "none" }}
            />
          </Form.Group>
          <Form.Group>
            <ButtonGroup aria-label="Basic example">
              <Button variant="primary" onClick={BoderSubmit}>
                
              </Button>
              <Link to="/">
                <Button variant="secondary"></Button>
              </Link>
            </ButtonGroup>
          </Form.Group>
        </Form>
      </div>
    );
  }
}

export default withRouter(BorderWrite);
